export default class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isDark: true
    };
  }

  componentDidMount() {
    localStorage.setItem("isDark", true);
  }

  render() {
    return (
      <div className="dark-mode">
        <StyleProvider
          value={{isDark: this.state.isDark, changeTheme: null}}
        >
          <Header />
          <Greeting />
          <Skills />
          <StackProgress />
          <Education />
          <WorkExperience />
          <Projects />
          {/* <StartupProject /> */}
          {/* <Achievement /> */}
          {/* <Blogs /> */}
          {/* <Talks /> */}
          {/* <Twitter /> */}
          {/* <Podcast /> */}
          <Profile />
          <Footer />
          <Top />
        </StyleProvider>
      </div>
    );
  }
}
